import {
    usersecession,
    userUpdate,
    changeProfile,
    donationList,
    followList,
  } from "@/api/mypage.js"
  
  const mypageStore = {
    namespaced: true,
    state: {
      donationList: [],
      followList: [],
      profileImage: '',
    },


    
    getters: {
      donationList(state) {
        return state.donationList;
      },
      followList(state) {
        return state.followList;
      },
      profileImage(state) {
        return state.profileImage
      }
    },



    mutations: {
      SET_DONATIONLIST: (state, donationList) => {
        state.donationList = donationList;
      },
      SET_FOLLOWLIST: (state, followList) => {
        state.followList = followList;
      },
      SET_PROFILEIMAGE: (state, profileImage) => {
        state.profileImage = profileImage;
      }     
    },



    actions: {
        
      //  
      //        
      async userSecession({ user_seq }) {
        await usersecession(
            user_seq,
          (response) => {
            console.log("");
            console.log(response);
            alert('~')
            this.$router.push('/login')
          },
          (error) => {
            console.log(error);
          }
        )
      },
      
      
      //  
      // updateInfo = { user_seq: , info: { userPassword: , userPhone: , userEmail: } }
      async updateUserInfo({ updateInfo }) {
        await userUpdate(
            updateInfo,
          (response) => {
            console.log("  ");
            console.log(response);
          },
          (error) => {
            console.log(error);
          }
        )
      },

      
      //  
      // profileInfo = { user_seq: , profile:   }  
      async changeUserProfile({ commit }, profileInfo) {
        await changeProfile(
            profileInfo,
          (response) => {
            console.log("     ");
            console.log(response);
            commit('SET_PROFILEIMAGE', response.data);
          },
          (error) => {
            console.log(error);
          }
        )
      },


      //   
      // profileInfo = { user_seq: , profile:   }  
      async getDonationList({ commit }, user_seq) {
        await donationList(
            user_seq,
          (response) => {
            console.log(" ");
            console.log(response);
            commit('SET_DONATIONLIST', response.data);
          },
          (error) => {
            console.log(error);
          }
        )
      },


      //    
      async getFollowerList({ commit }, user_seq) {
        await followList(
            user_seq,
          (response) => {
            console.log("  ");
            console.log(response);
            commit('SET_DONATIONLIST', response.data);
          },
          (error) => {
            console.log(error);
          }
        )
      },


    },
  };
  
  export default mypageStore;