<template>
    <div>Mypage</div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
const mypageStore = "mypageStore";


export default {
  name: "MypageView",


  data() {
    return {
    };
  },


  computed: {
    ...mapGetters(mypageStore, [
      "donationList",
      "followList",
      "profileImage",
    ]),
  },


  methods: {
    ...mapActions(mypageStore, [
      "userSecession",
      "updateUserInfo",
      "changeUserProfile",
      "getDonationList",
      "getFollowerList",
    ]),

    // 
    secession() {
      //  user_seq  
      this.userSecession()
    },

    //  
    updateInfo() {
      // updateInfo = { user_seq: , info: { userPassword: , userPhone: , userEmail: } }
      this.updateUserInfo(this.updateInfo)
    },

    //  
    changeProfile() {
      // profileInfo = { user_seq: , profile:   }  
      this.changeUserProfile(this.profileInfo)
    }
  },


  created() {
    //  user_seq  
    this.getDonationList()
    this.getFollowerList()
  },


  mounted() {

  },  
}
</script>

