          <input class="usernameInput" v-model.trim="loginCredentials.userId" type="text" id="username" placeholder="ID  ." required>

          <label class="password" for="password">Password:</label>
          <input class="passwordInput" v-model.trim="loginCredentials.userPassword" type="password" id="password" placeholder="  ." required>

          <button class="login" style="vertical-align: middle;" type="submit"><p style="position:absolute; top: -1vh; left: 2vw;"></p></button>
      </form>
      <div @click="changeLoginSignup" class="changeButton">Signup</div>
    </div>



    <!-- signup form -->
    <div v-if="showLogin && !LoginSignup" data-aos="fade-up" data-aos-duration="500" class="loginForm">
