        @submit.prevent
      >
        <label for="userId" class="id">:</label>
        <input
          v-model.trim="signupCredentials.userId"
          type="text"
          class="idInput"
          id="userId"
          maxlength="10"
          placeholder="ID (10)"
          @input="idChange"
          required
        />
        <button v-show="!IdCheck" @click.prevent="userCheckID" type="submit" class="idcheck"><p style="font-size: 4vh; "></p></button>

          <label for="userPassword" class="signpuPassword">:</label>
          <input
