    // example.style.clip = `rect(${200 - (scrollY - 500 )}px, 10000px, 200px, 0px)`
    //     (   )
    //  :   
    //  :      - scrollY

    setTimeout(() => {
      //    data 

      this.showLogoLoding = false; // 3   
      
      //   
      const startLogo = document.getElementById('startLogo')
      startLogo.style.opacity = 0

      //    
      const example = document.getElementById('example')
      // console.log(example)
      example.style.clip = `rect(${1000 - scrollY}px, 10000px, 10000px, 0px)`
      
      
      //   
      document.addEventListener("scroll", this.scroll);


      // const elts = {
      //   text1: document.getElementById("text1"),
      //   text2: document.getElementById("text2")
      // };

      // const texts = [
      //   "",
      //   "AI",
      //   // " ??",
      //   // "",
      //   // "",
      //   // "",
      //   // "",
      //   // "",

      // ];

      // const morphTime = 3;
      // const cooldownTime = 0.25;

      // let textIndex = texts.length - 1;
      // let time = new Date();
      // let morph = 0;
      // let cooldown = cooldownTime;

      // elts.text1.textContent = texts[textIndex % texts.length];
      // elts.text2.textContent = texts[(textIndex + 1) % texts.length];

      // function doMorph() {
      //   morph -= cooldown;
      //   cooldown = 0;

      //   let fraction = morph / morphTime;

      //   if (fraction > 1) {
      //       cooldown = cooldownTime;
      //       fraction = 1;
      //   }

      //   setMorph(fraction);
      // }

      // function setMorph(fraction) {
      //   elts.text2.style.filter = `blur(${Math.min(8 / fraction - 8, 100)}px)`;
      //   elts.text2.style.opacity = `${Math.pow(fraction, 0.4) * 100}%`;

      //   fraction = 1 - fraction;
      //   elts.text1.style.filter = `blur(${Math.min(8 / fraction - 8, 100)}px)`;
      //   elts.text1.style.opacity = `${Math.pow(fraction, 0.4) * 100}%`;

      //   elts.text1.textContent = texts[textIndex % texts.length];
      //   elts.text2.textContent = texts[(textIndex + 1) % texts.length];
      // }

      // function doCooldown() {
      //   morph = 0;

      //   elts.text2.style.filter = "";
      //   elts.text2.style.opacity = "100%";

      //   elts.text1.style.filter = "";
      //   elts.text1.style.opacity = "0%";
      // }

      // function animate() {
      //   requestAnimationFrame(animate);

      //   let newTime = new Date();
      //   let shouldIncrementIndex = cooldown > 0;
      //   let dt = (newTime - time) / 1000;
      //   time = newTime;

      //   cooldown -= dt;

      //   if (cooldown <= 0) {
      //       if (shouldIncrementIndex) {
      //           textIndex++;
      //       }

      //       doMorph();
      //   } else {
      //       doCooldown();
      //   }
      // }

      // animate();
      const v = document.getElementById('test')
      v.scrollTop = 3000
    }, 3000);
  },
  unmounted() {
    document.removeEventListener("scroll", this.scroll);
  },
}
</script>

