<template>
<!--   -->
    <div class="donaForm">
      <h1>Donation</h1>
      <!--   -->
      <div class="container d-flex">
        <b class="col"> : {{this.selOrganizationInfo.name}} </b>
        <b class="col"> <b v-if="this.donationInfo.donationImgUrl"> </b></b>
        <b class="col">: <b v-if="!this.donationInfo.donationText">  </b> <b v-if="this.donationInfo.donationText">{{this.donationInfo.donationText.slice(0,10)}}</b> </b>
        <b class="col">: {{this.donationInfo.donationPay}} </b>
      </div>
      <!--     -->
      <div class="container d-flex">
        <button @click.prevent class="col nowForm" :class="{now: this.stage.one}" style="{{this.stage.one}}? now:"> </button>
        <button @click.prevent class="col nowForm" :class="{now: this.stage.two}"> </button>
        <button @click.prevent class="col nowForm" :class="{now: this.stage.three}"> </button>
        <button @click.prevent class="col nowForm" :class="{now: this.stage.four}"> </button>
      </div>
      <!--    -->
      <div v-show="this.organizationList && this.stage.one">
        <h1> </h1>
        <!--    -->
        <div class="card" style="width: 18rem;">
            <img v-bind:src=this.selOrganizationInfo.logURL alt="" class="card-img-top">
            <div class="card-body">
              <h5 class="card-title">{{this.selOrganizationInfo.name}}</h5>
            </div>
          </div>
        <!--   -->
        <div v-for="(organization, index) in this.organizationList" :key="`organizationList-${index}`" class="organizationList">
          <div class="card" style="width: 18rem;">
            <img v-bind:src=organization.foundationUrlLogo alt="" class="card-img-top">
            <div class="card-body">
              <h5 class="card-title">{{organization.foundationName}}</h5>
              <p class="card-text">{{organization.foundationContent}}</p>
              <button @click="selOrg(organization.foundationName, organization.foundationLogoUrl, organization.foundationSeq)"> </button>
            </div>
          </div>
        </div>
      </div>
      <!--    -->
      <div v-show="this.postcardList && this.stage.two">
        <h1> </h1>
        <!--      -->
        <!--    -->
          <!-- <img v-bind:src=this.donationInfo.donationImgUrl alt=""> -->
        <!--   -->
        <!-- <div v-for="(postcard, index) in this.postcardList" :key="`postcardList-${index}`" class="postcardList"> -->
          <!-- <img v-bind:src=postcard.postcardImgUrl alt="" @click="selPostcard(postcard.postcardImgUrl)"> -->
        <!-- </div> -->
      </div>
      <!--   -->
      <div v-show="this.stage.three" class="textBack">
        <!--      -->
        <input v-model="this.donationInfo.donationText" type="text">
      </div>
      <!--    -->
      <form v-show="this.stage.four">
        <label for="pay">  : </label>
        <input v-model="this.donationInfo.donationPay" type="number" id="pay">
      </form>
      <div>
        <b>{{errorMSG}}</b>
        <button @click.prevent="preStage" v-show="!this.stage.one"></button>
        <button @click.prevent="nextStage" v-show="!this.stage.four"></button>
        <button @click="pay" v-show="this.stage.four"></button>
      </div>
    </div>
</template>
  
  <script>
  import { mapActions, mapGetters } from "vuex";
  const donationStore = "donationStore";
  const organizationStore = "organizationStore";
  const postcardStore = "postcardStore";
  const accountStore = "accountStore";

  export default {
    name:"OrganizationListView",
    data() {
    return {
        //   
        donationInfo : {
          donationImgUrl: null,
          donationPay: 0,
          donationText: "",
          foundationSeq: null,
          //    
        },
        //  
        selOrganizationInfo: {
          name: null,
          logoURL: null,
        },
        //   
        dummy: 1,
        //   
        stage: {
          one: true,
          two: false,
          three: false,
          four: false,
        },
        //  
        errorMSG: null,
      };
    },


    computed: {
      ...mapGetters(organizationStore, ["organizationList"]),
      ...mapGetters(postcardStore, ["postcardList"]),
      ...mapGetters(accountStore, ['userInfo']),
    },


    methods: {
      ...mapActions(donationStore, ["doDonate"]),
      //   
      ...mapActions(organizationStore, ["getFoundationList"]),
      //   
      ...mapActions(postcardStore, ["userPostcardList"]),
      //   
      nextStage() {
        // 1 > 2 
        if (this.stage.one) {
          //    
          if (!this.selOrganizationInfo.name) {
            this.errorMSG = " "
          } else {
            //      
            this.stage.one = false
            this.stage.two = true
            this.errorMSG = ""
          }
        // 2 > 3
        } else if (this.stage.two) {
          //  
          this.donationInfo.donationImgUrl = this.dummy
          //    
          if (!this.donationInfo.donationImgUrl) {
            this.errorMSG = " "
          } else {
            //      
            this.stage.two = false
            this.stage.three = true
            this.errorMSG = ""
          }
        // 3 > 4
        } else if (this.stage.three) {
          this.stage.three = false
          this.stage.four = true
        }
      },
      //  
      preStage() {
        if (this.stage.two) {
          this.stage.two = false
          this.stage.one = true
        } else if (this.stage.three) {
          this.stage.three = false
          this.stage.two = true
        } else {
          this.stage.four = false
          this.stage.three = true
        }
      },
      // 
      donate() {
        console.log({...this.donationInfo, userSeq: this.userInfo.userSeq})
        this.doDonate({...this.donationInfo, userSeq: this.userInfo.userSeq})
      }, 
      //  
      selOrg(name, URL, Seq){
        this.selOrganizationInfo.name = name
        this.selOrganizationInfo.logoURL = URL
        this.donationInfo.foundationSeq = Seq
        this.errorMSG = ""
      },
      //   
      selPostcard(URL) {
        this.donationInfo.donationImgUrl = URL
        this.errorMSG = ""
      },
      // 
      pay() {
        const { IMP } = window
        //     
        IMP.init('imp40811644')
        IMP.request_pay({
          pg : 'html5_inicis',
          pay_method: "card",
          merchant_uid: `${this.selOrganizationInfo.name}-donation` + new Date().getTime(),
          name : this.selOrganizationInfo.name , //  
          amount: this.donationInfo.donationPay,
          buyer_tel: this.userInfo.userPhone,
          buyer_name: this.userInfo.userName,
        }, function(rsp) {
          console.log(rsp);
          if ( rsp.success ) {
            //    
            //      
            console.log(rsp.success)
          } else {
            console.log(rsp.error_msg);
          }
        });
      },
      //  
      reset () {
        this.donationInfo = { donationImgUrl: null, donationPay: 0,
          donationText: "",foundationSeq: null,};
        this.selOrganizationInfo =  {name: null, logoURL: null };
        this.stage = {one: true, two: false, three: false, four: false};
        this.errorMSG = null;
      },
    },
    created() {
      if (!this.userInfo) {
        this.$router.push({name: "beginningView"})
      }
      //      
      if (!this.organizationList.length){
        this.getFoundationList()
      }
      if (!this.postcardList.length) {
        // this.userPostcardList(this.userInfo.userSeq)
        //      
        this.userPostcardList(this.dummy)
      }
    },


    mounted() {
      this.reset
    },  
  }
  </script>
  
  <style>
  .donaForm {
    width: 90vw;
    height: 90vh;
    background-color: antiquewhite;
  }
  .nowForm {
    margin: auto;
  }
  .now {
    background-color: aquamarine;
  }
  .organizationclass {
    background-color: skyblue;
  }
  .organizationItem {
    background-color: antiquewhite;
  }
  .textBack {
    background: url("@/assets/Back.png");
    background-position: center bottom;
    background-repeat: no-repeat;
    background-size: cover;
    width: 50vw;
    height: 25vh;
    align-self: center;
  }
  </style>