// import axios from "axios";
import { apiInstance } from "./index.js";
const api = apiInstance();

//  
// donationInfo = {
//   "donationImgUrl": "string",
//   "donationPay": 100000,
//   "donationText": "",
//   "foundationSeq": 1,
//   "userSeq": 1
// }

//
async function donate(donationInfo, success, fail) {
  await api
    .post(`/api/donations`, JSON.stringify(donationInfo))
    .then(success)
    .catch(fail);
}

